import React, { useEffect, useState } from "react";
import Layout from '../layout/Layout';
import ShowBlog from '../components/ShowBlog';
import Metadata from '../components/Metadata';
import { BlogsPageMetadata } from '../configs/PageMetadata';
import { BlogGetAll } from "../services/BlogService";
import BlogSkeleton from '../components/BlogSkeleton';

export default function BlogsPage() {
  console.log('Blogs');
  //const blogId = "20210312064310"

  const [blogs, setBlogs] = useState(null);
  useEffect(() => {
    // debugger;
    BlogGetAll()
      .then((response) => {
        if (response !== undefined) {
          setBlogs(response);
        }
      })
      .catch(error => {
        console.log(error);
      });
  }, []);

  return (
    <Layout>
      <Metadata
        Title={BlogsPageMetadata.Title}
        Description={BlogsPageMetadata.Description}
      />
      { blogs === null ?
        <BlogSkeleton /> :
        blogs.map(function (blog, index) {
          return (
            <React.Fragment key={index}>
              <ShowBlog blog={blog} />
            </React.Fragment>
          )
        })
      }      
    </Layout>
  );
}